@import "https://fonts.googleapis.com/css2?family=Kanit&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Kanit, sans-serif;
}

body {
  background-image: url("bg.c39dd194.jpg");
  background-position: center;
  background-size: fill;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  display: flex;
}

.calculator {
  width: 300px;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 20px 20px 200px #00ccf0;
}

form input {
  width: 100%;
  height: 130px;
  color: #00ccf0;
  text-align: center;
  pointer-events: none;
  background-color: #03213da9;
  border: none;
  border-radius: 12px;
  padding: 1rem;
  font-size: 2rem;
  box-shadow: 20px 20px 200px #00ccf0;
}

form input::placeholder {
  color: #add8e649;
}

.buttons {
  background-color: #0000003b;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 20px;
  display: flex;
}

button {
  width: 60px;
  height: 52px;
  cursor: pointer;
  color: #fff;
  background: #013f6680;
  border: none;
  border-radius: 5px;
  flex: 0 0 22%;
  margin: 5px 0;
  font-size: 22px;
  font-weight: 600;
  box-shadow: 2px 2px 5px #32bbcd;
}

button:hover {
  color: #fff;
  background: #0bd5f09d;
  scale: 1.2;
  box-shadow: 4px 4px 5px #f5f5f5;
}

.btn-yellow {
  color: #f5f5f5;
  background: #00000080;
}

.btn-btn-grey {
  color: #fff;
  background: #ff69b4;
}

.btn-equal {
  background: #00ff2a5d;
}

.btn-clear {
  background: #ff000063;
}

